<template>
    <div>
        <div>
        <input type="radio" value="day" v-model="timePeriod" @click="setTimePeriod('day')"> Day
        <input type="radio" value="month" v-model="timePeriod" @click="setTimePeriod('month')"> Month
        <input type="radio" value="year" v-model="timePeriod" @click="setTimePeriod('year')"> Year
        </div>
        <line-chart></line-chart>
    </div>
</template>

<script>
    //import { Line } from 'vue-chartjs';

    export default {
    data() {
        return {
        chartData: {
            // Data for the chart goes here
        },
        timePeriod: 'day' // Initial time period
        }
    },
    methods: {
        // Method to update the chart data and redraw the chart
        updateChart() {
        this.renderChart(this.chartData, {responsive: true, maintainAspectRatio: false});
        },
        // Method to set the time period based on the selected radio button
        setTimePeriod(period) {
        this.timePeriod = period;
        // Update the chart data based on the selected time period
        // and redraw the chart
        this.updateChart();
        }
    },
    mounted() {
        // Set the initial data for the chart
        this.chartData = {
        labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July'],
        datasets: [
            {
            label: 'Total Sales',
            backgroundColor: '#f87979',
            data: [40, 39, 10, 40, 39, 80, 40]
            }
        ]
        };
        // Render the chart
        this.updateChart();
    }
    }
</script>